import styled from 'styled-components';

const ScanlineOverlay = styled.div`
  pointer-events: none;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  mix-blend-mode: overlay;
  opacity: 0.4; /* Increased from 0.25 for stronger effect */
  background: repeating-linear-gradient(
    to bottom,
    transparent,
    transparent 1px,
    rgba(0,0,0,0.85) 3px,   /* Thicker and darker scanline */
    transparent 5px,
    transparent 7px
  );
`;

export const StaticScanlines = () => <ScanlineOverlay />;