import { createGlobalStyle } from 'styled-components';
import RoslindaleUltraWoff2 from '../fonts/RoslindaleDisplay-Ultra.woff2';
import RoslindaleUltraWoff from '../fonts/RoslindaleDisplay-Ultra.woff';
import RoslindaleUltraOtf from '../fonts/RoslindaleDisplay-Ultra.otf';

export const GlobalStyles = createGlobalStyle`
  @font-face {
    font-family: 'Roslindale Ultra';
    src: 
      url(${RoslindaleUltraWoff2}) format('woff2'),
      url(${RoslindaleUltraWoff}) format('woff'),
      url(${RoslindaleUltraOtf}) format('opentype');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
  }

  body {
    font-family: 'Roslindale Ultra', serif;
    background: #111;
    color: #fff;
    margin: 0;
    padding: 0;
  }
`;